.about,
.about-ru {
  font-family: var(--ff-second);
  font-weight: 500;
  font-size: var(--fz-subtitle);

  &__description {
    margin: 0 auto;
    padding: 1em 2em;
    width: fit-content;
    max-width: 60ch;
    text-wrap: balance;

    p {
      margin-bottom: 1em;
    }
  }

  &__experience {
    padding: 1rem 2rem;

    &_title {
      text-align: left;
      text-decoration-line: underline;
      text-underline-offset: 0.5rem;
      text-decoration-style: dotted;
    }
  }

  &__achievements {
    padding-block: 1em;
    list-style-type: disc;
    list-style-position: inside;

    li {
      padding: 0.5em 2em;
      max-width: 60ch;
      font-weight: 400;
      font-size: var(--fz-text);
    }
  }
}

.about-ru {
  display: none;
}

[lang="ru"] {
  .about-ru {
    display: block;
  }

  .about {
    display: none;
  }
}
